.footer-container {
  height: auto;
  width: 100%;
  position: relative;
}

.footer-margin {
  height: 20vh;
  width: 100%;
  position: absolute;
  top: 0vh;
}

.footer {
  height: auto;
  width: 100%;
  position: relative;
  top: 20vh;
  border: 1px;
  border-top-style: solid;
  background-color: lightgray;
}

.footer-upper {
  display: flex;
  height: auto;
  position: absolute;
  width: 100%;
  top: 0vh;
}

.footer-upper p {
  font-size: 25px;
  padding-left: 15px;
  padding-right: 10px;
}

.icons {
  padding: 23px 6px;
}

.footer-lower {
  position: absolute;
  width: 100%;
  height: auto;
  top: 10vh;
}

.copyright {
  position: absolute;
  top: 50px;
  left: 15px;
  font-size: 20px;
}
.lastText {
  font-size: 12px;
  position: absolute;
  top: 100px;
  left: 15px;
  right: 15px;
}

@media only screen and (max-width: 600px) {
  .copyright {
    font-size: 16px;
  }

  .footer-upper p {
    font-size: 20px;
    padding-right: 4px;
    padding-left: 10px;
  }

  .icons {
    padding: 23px 2px;
  }
}
